export default {
	data() {
		return {
			title: 'Hello',
			news: []
		}
	},
	onPullDownRefresh() {
		uni.showToast({
		        title: '标题',
		        duration: 2000
		    });
	},
	onLoad() {
		uni.showLoading({
			title: '',
			mask: false
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news = res.data;
				uni.hideLoading();
				
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openInfo(e){
			let news = e.currentTarget.dataset.news;
			getApp().globalData.currentNewsInfo = news;
			uni.navigateTo({
				url: '../info/info'
			});
		}
	}
}
